
@use '../abstracts/variables' as *;
@use '../abstracts/mixins' as *;

@use "sass:color";

.intro {
    width: 100%;
    min-height: 100vh;
    background: linear-gradient(to top, var(--background), transparent), url(../img/mauricio-carrasco-nordic-giant.webp) center / cover no-repeat;
    position: relative;
    
    @include mq('lg') {
        background: linear-gradient(to top, var(--background), transparent), url(../img/sloto-paint-viking-24-backs-213.webp) center / cover no-repeat;
    }
    
    @include mq('xl') {
        background: linear-gradient(to top, var(--background), transparent), url(../img/deiv-calviz-david-villegas-viking-deiv-calviz.webp) center / cover no-repeat;
    }
    
    @include mq('2xl') {
        background: linear-gradient(to top, var(--background), transparent), url(../img/artforgame-gamedesign-background.webp) center / cover no-repeat;
    }

    &-inner {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        text-align: center;
    }
}

.pattern {
    position: absolute;
    top: 1em;
    left: 0;
    width: 100%;
    height: 36px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;

    &-icon {
        height: 100%;

        img {
            height: 100%;
            padding: 0 .5em;
            padding-inline: .5em;
        }
    }

    &-line {
        flex: 1 1 40%;
        width: 100%;
        height: 80%;
        background-image: url(../img/nordic-pattern.svg);
        background-repeat: repeat-x;
        background-position: left center;
        background-size: contain;
    }

    &-right {
        background-position: right center;
    }
}

.index {
    margin-top: 3em;
    
    &-title {
        margin-top: 1em;
        margin-bottom: 1em;
        text-align: center;
        
        @include mq('sm') {
            font-size: 3em;
        }
    }
    
    &-headings {
        width: 100%;
        margin-left: auto;
        margin-right: auto;
        margin-inline: auto;

        @include mq('sm') {
            width: 75%;
        }
    }

    ol {
        padding-left: 1.2em;
    }

    ul {
        padding-left: 0;
        list-style: none;
    }

    ul li:before {
        display: inline-block;
        width: 40px;
        height: 40px;
        margin-right: 0.3em;
        content: url(../img/viking-axe.svg);
    }

    .chapter-title {
        font-weight: 600;
    }

    .main-heading {
        font-weight: 700;
    }

    .sub-headings {
        margin-top: .75em;
    }

    li {
        margin-bottom: .75em;
    }

    ol {
        font-size: 1em;
        
        @include mq('sm') {
            font-size: 1.5em;
        }
    }

    ol ul {
        font-size: .9em;
    }

    a {
        color: inherit;
        text-decoration: underline dotted;
        transition: color 200ms ease-in;
    }

    a:hover, a:focus {
        color: var(--clr-primary);
        text-decoration: none;
    }
}

.line {
    width: 100%;
    display: flex;
    justify-content: center;

    svg {
        max-width: 1000px;
    }
}
